h2 {
  text-align: center;
}
h3 {
  text-align: center;
}

.other-events {
  padding: 60px 0;
}

.row {
  margin: 0 auto;
}

div.box {
  margin: 0.001% 0.001% 0.001% 0.001%;
  clear: none;
  height: 300px;
  width: 50%;
  overflow: hidden;
}

div.box {
  cursor: pointer;
}

.modal-open {
  overflow: hidden;
  padding-right: 0 !important;
}

body:not(.modal-open){
  padding-right: 0px !important;
}

.imageBlock {
  width: 100%;
  height: 100%;
}

.imageInBox {
  position: relative;
}

.btnBox {
  position: absolute;
}

#contentRedLine {
  padding: 0;
}

.shadow {
  background-color: #00000085;
}

/* .content-wrapper {
} */

.textRedLine {
  padding: 12px 50px 24px;
  color: white;
}

.title-redline {
  padding: 12px 0;
  color: #fff;
}

.gallery {
  height: 100vh;
  position: relative;
}

.gallery-info {
  position: absolute;
  z-index: 9;
  top: 30%;
}

.swiper-container {
  height: 100%;
}

.gallery-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.counter {
  text-align: center;
  padding: 60px 60px 120px;
}
