.intro {
  position: relative; /* This protects the inner element from being blurred */
  background-image: url("../assests/intro.jpg");
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-shadow: inset 0px 0px 0px 999px rgba(0, 0, 0, 0.6);
  filter: grayscale(-1%) contrast(200%) brightness(60%);
  -webkit-filter: grayscale(0) contrast(100%);
}

.bigTitle {
  margin: 0;
  color: #fff;
  font-size: 10rem;
}

.introTitle {
  color: #fff;
  margin: 0;
  font-size: 4.5rem;
  text-align: center;
}

.subTitle {
  color: #fff;
  font-size: 1.6rem;
  margin-top: 20px;
}

.register-button {
  padding: 13px 26px;
  border: 1px solid #fff;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  line-height: 24px;
  transition: all 300ms ease 0.803859ms;
  transition-property: all;
  transition-duration: 0.3s;
  transition-timing-function: ease;
  transition-delay: 0s;
  -webkit-transition: all 0.3s ease;
}

.register-button:hover {
  background-color: #fff;
  color: #222;
  opacity: 0.9;
}

.number-count-down {
  font-size: 6rem;
  display: block;
  color: #fff;
  width: 100%;
  font-weight: 400;
  margin-bottom: 0.3em;
}

.counter-render {
  color: #fff;
}

@media (max-width: 768px) {
  .bigTitle {
    font-size: 7rem;
  }
  .introTitle {
    font-size: 2.5rem;
  }

  .intro {
    margin-top: 7rem;
  }

  iframe {
    width: 100%;
  }
}
